interface AdvancedDiseaseInfo {
  id: string;
  name: string;
  hindiName: string;
  crop: string;
  hindiCrop: string;
  severity: 'Low' | 'Medium' | 'High' | 'Critical';
  confidence: number;
  
  // Advanced Visual Analysis
  visualPatterns: {
    colors: [number, number, number][];
    shapes: string[];
    textures: string[];
    patterns: string[];
    leafConditions: string[];
  };
  
  // Comprehensive Symptoms
  symptoms: {
    visual: string[];
    hindiVisual: string[];
    physical: string[];
    hindiPhysical: string[];
    environmental: string[];
    hindiEnvironmental: string[];
  };
  
  // Advanced Treatment
  treatment: {
    immediate: string[];
    hindiImmediate: string[];
    preventive: string[];
    hindiPreventive: string[];
    organic: string[];
    hindiOrganic: string[];
    chemical: string[];
    hindiChemical: string[];
    followUp: string[];
    hindiFollowUp: string[];
  };
  
  // Seasonal & Regional Factors
  seasonality: {
    commonMonths: string[];
    riskFactors: string[];
    prevention: string[];
  };
  
  // Advanced Metadata
  metadata: {
    spreadRate: 'Slow' | 'Medium' | 'Fast' | 'Very Fast';
    economicImpact: 'Low' | 'Medium' | 'High' | 'Severe';
    treatmentDifficulty: 'Easy' | 'Medium' | 'Hard' | 'Very Hard';
    organicTreatmentAvailable: boolean;
  };
}

